import { StrictMode } from "react";
import { createRoot } from "react-dom/client";
import { createBrowserRouter, createHashRouter, RouterProvider } from "react-router-dom";

import App from "./components/App"; // Import App component
import HomePage from "./pages/home";
import Quiz from "./pages/quiz/components/Quiz";
import FillInQuiz from "./pages/quiz/components/FillInQuiz";

import SystemSettingsPage from "./pages/systemSettings";
import NotebookManagementPage from "./pages/notebookManagement";
import NavigationBlocker from "./components/NavigationBlocker";
import Reading from "./pages/Reading/components/Reading";
import AnkiDemo from "./pages/anki"; // Anki Demo
import DiagnosticsPage from "./pages/diagnostics"; // 

const rootElement = document.getElementById("root");
const root = createRoot(rootElement);

//  basename
// - Capacitor (capacitor://  http://localhost )  "/"
// - Web  PUBLIC_URL  "."  PUBLIC_URL
// -  "/"
const isCapacitorLike = () => {
  const href = window.location.href;
  return (
    href.startsWith("capacitor://") ||
    href.startsWith("http://localhost") ||
    href.startsWith("https://localhost")
  );
};
const publicUrl = process.env.PUBLIC_URL;
const resolvedBasename = isCapacitorLike()
  ? "/"
  : publicUrl && publicUrl !== "."
  ? publicUrl
  : "/";

const routes = [
  {
    path: "/",
    element: <App />,
    children: [
      {
        index: true,
        element: <HomePage />,
      },
      {
        path: "quiz",
        element: (
          <NavigationBlocker
            clearOnConfirm
            considerQuiz={false}
            considerReading={true}
            considerFillin={true}
            message=""
          >
            <Quiz />
          </NavigationBlocker>
        ),
      },
      {
        path: "fillin",
        element: (
          <NavigationBlocker
            clearOnConfirm
            considerQuiz={true}
            considerReading={true}
            considerFillin={false}
            message=""
          >
            <FillInQuiz />
          </NavigationBlocker>
        ),
      },
      {
        path: "anki",
        element: (
          <NavigationBlocker
            clearOnConfirm
            message=""
          >
            <AnkiDemo />
          </NavigationBlocker>
        ),
      },
      {
        path: "notebook-management",
        element: (
          <NavigationBlocker
            clearOnConfirm
            message=""
          >
            <NotebookManagementPage />
          </NavigationBlocker>
        ),
      },
      {
        path: "settings",
        element: (
          <NavigationBlocker
            clearOnConfirm
            message=""
          >
            <SystemSettingsPage />
          </NavigationBlocker>
        ),
      },
      {
        path: "reading",
        element: (
          <NavigationBlocker
            clearOnConfirm
            considerQuiz={true}
            considerReading={false}
            considerFillin={true}
            message=""
          >
            <Reading />
          </NavigationBlocker>
        ),
      },
      {
        path: "diagnostics",
        element: <DiagnosticsPage />,
      },
    ],
  },
];

const router = isCapacitorLike()
  ? createHashRouter(routes)
  : createBrowserRouter(routes, { basename: resolvedBasename });

import { AppProvider } from "./store/contexts/AppContext"; // Import AppProvider

root.render(
  <StrictMode>
    <AppProvider>
      <RouterProvider router={router} />
    </AppProvider>
  </StrictMode>
);
