import React, { useEffect, useMemo, useRef, useState } from "react";
import { Capacitor } from "@capacitor/core";
import { TextToSpeech } from "@capacitor-community/text-to-speech";
import { VoiceRecorder } from "capacitor-voice-recorder";

const Row = ({ label, value }) => (
  <div style={{ display: "flex", gap: 8, margin: "6px 0" }}>
    <div style={{ width: 160, color: "#666" }}>{label}</div>
    <div style={{ fontFamily: "monospace" }}>{String(value)}</div>
  </div>
);

const Box = ({ title, children }) => (
  <div style={{ border: "1px solid #ddd", borderRadius: 8, padding: 12, marginTop: 12 }}>
    <div style={{ fontWeight: 600, marginBottom: 8 }}>{title}</div>
    {children}
  </div>
);

export default function DiagnosticsPage() {
  const [logs, setLogs] = useState([]);
  const [ttsOk, setTtsOk] = useState(null);
  const [webTtsOk, setWebTtsOk] = useState(null);
  const [vrPerm, setVrPerm] = useState(null);
  const [vrOk, setVrOk] = useState(null);
  const [vrErr, setVrErr] = useState("");
  const [recUrl, setRecUrl] = useState("");
  const [webRecOk, setWebRecOk] = useState(null);
  const [webRecErr, setWebRecErr] = useState("");
  const audioRef = useRef(null);

  const isNative = Capacitor?.isNativePlatform?.() === true;
  const platform = Capacitor?.getPlatform?.() || "unknown";
  const isTTSPluginAvailable =
    isNative && typeof Capacitor?.isPluginAvailable === "function"
      ? Capacitor.isPluginAvailable("TextToSpeech")
      : false;
  const isVRPluginAvailable =
    isNative && typeof Capacitor?.isPluginAvailable === "function"
      ? Capacitor.isPluginAvailable("VoiceRecorder")
      : false;

  const isWebSpeech =
    typeof window !== "undefined" &&
    "speechSynthesis" in window &&
    typeof window.SpeechSynthesisUtterance !== "undefined";

  const isWebMediaSupported =
    typeof navigator !== "undefined" &&
    navigator.mediaDevices &&
    typeof navigator.mediaDevices.getUserMedia === "function" &&
    typeof window !== "undefined" &&
    "MediaRecorder" in window;

  const log = (m) => setLogs((prev) => [...prev, `[${new Date().toLocaleTimeString()}] ${m}`]);

  useEffect(() => {
    const unlock = () => {
      try { const a = new Audio(); a.play().catch(() => {}); } catch (_) {}
      window.removeEventListener("touchstart", unlock);
      window.removeEventListener("click", unlock);
    };
    window.addEventListener("touchstart", unlock, { once: true });
    window.addEventListener("click", unlock, { once: true });
    return () => {
      window.removeEventListener("touchstart", unlock);
      window.removeEventListener("click", unlock);
    };
  }, []);

  const testNativeTTS = async () => {
    setTtsOk(null);
    try {
      if (!isTTSPluginAvailable) {
        log("TTS  ( sync  APK)");
        setTtsOk(false);
        return;
      }
      await TextToSpeech.speak({ text: "", lang: "ja-JP", rate: 1.0 });
      await TextToSpeech.speak({ text: "", lang: "zh-TW", rate: 1.0 });
      setTtsOk(true);
      log(" TTS ");
    } catch (e) {
      setTtsOk(false);
      log(` TTS : ${e?.message || e}`);
    }
  };

  const testWebTTS = async () => {
    setWebTtsOk(null);
    try {
      if (!isWebSpeech) {
        setWebTtsOk(false);
        log("Web speechSynthesis ");
        return;
      }
      const u1 = new SpeechSynthesisUtterance("");
      u1.lang = "ja-JP"; u1.rate = 1.0;
      const p1 = new Promise((res) => { u1.onend = res; });
      window.speechSynthesis.speak(u1);
      await p1;

      const u2 = new SpeechSynthesisUtterance("");
      u2.lang = "zh-TW"; u2.rate = 1.0;
      const p2 = new Promise((res) => { u2.onend = res; });
      window.speechSynthesis.speak(u2);
      await p2;

      setWebTtsOk(true);
      log("Web TTS ");
    } catch (e) {
      setWebTtsOk(false);
      log(`Web TTS : ${e?.message || e}`);
    }
  };

  const checkVRPermission = async () => {
    try {
      if (!isVRPluginAvailable) {
        setVrPerm(false);
        log("VoiceRecorder ");
        return;
      }
      const has = await VoiceRecorder.hasAudioRecordingPermission();
      setVrPerm(!!has?.value);
      log(`: ${!!has?.value}`);
    } catch (e) {
      setVrPerm(false);
      log(`: ${e?.message || e}`);
    }
  };

  const requestVRPermission = async () => {
    try {
      if (!isVRPluginAvailable) { setVrPerm(false); log("VoiceRecorder "); return; }
      await VoiceRecorder.requestAudioRecordingPermission();
      await checkVRPermission();
    } catch (e) {
      log(`: ${e?.message || e}`);
    }
  };

  const testNativeRecord = async () => {
    setVrErr(""); setVrOk(null); setRecUrl("");
    try {
      if (!isVRPluginAvailable) { setVrOk(false); setVrErr(""); return; }
      const has = await VoiceRecorder.hasAudioRecordingPermission();
      if (!has?.value) { setVrOk(false); setVrErr(""); return; }
      await VoiceRecorder.startRecording();
      log(" 1.5 ");
      await new Promise((r) => setTimeout(r, 1500));
      const res = await VoiceRecorder.stopRecording();
      const { recordDataBase64, mimeType } = res?.value || {};
      if (recordDataBase64) {
        const mime = mimeType || "audio/aac";
        const url = `data:${mime};base64,${recordDataBase64}`;
        setRecUrl(url);
        setVrOk(true);
        log(` mime=${mime} len=${recordDataBase64.length}`);
      } else {
        setVrOk(false);
        setVrErr("stopRecording ");
      }
    } catch (e) {
      setVrOk(false);
      setVrErr(e?.message || String(e));
      log(`: ${e?.message || e}`);
    }
  };

  const testWebRecord = async () => {
    setWebRecErr(""); setWebRecOk(null); setRecUrl("");
    if (!isWebMediaSupported) { setWebRecOk(false); setWebRecErr(" MediaRecorder"); return; }
    try {
      const stream = await navigator.mediaDevices.getUserMedia({ audio: { echoCancellation: true, noiseSuppression: true } });
      let media;
      try { media = new MediaRecorder(stream, { mimeType: "audio/webm" }); } catch (_) { media = new MediaRecorder(stream); }
      const parts = [];
      media.ondataavailable = (ev) => { if (ev.data && ev.data.size > 0) parts.push(ev.data); };
      media.start();
      log(" Web  1.5 ");
      await new Promise((r) => setTimeout(r, 1500));
      await new Promise((r) => { media.onstop = r; media.stop(); });
      const blob = new Blob(parts, { type: "audio/webm" });
      const url = URL.createObjectURL(blob);
      setRecUrl(url);
      setWebRecOk(true);
      log(`Web  size=${blob.size}`);
    } catch (e) {
      setWebRecOk(false);
      setWebRecErr(e?.message || String(e));
      log(`Web : ${e?.message || e}`);
    }
  };

  return (
    <div style={{ padding: 16 }}>
      <h2></h2>

      <Box title="">
        <Row label="isNative">{isNative ? "true" : "false"}</Row>
        <Row label="platform">{platform}</Row>
        <Row label="TTS ">{isTTSPluginAvailable ? "true" : "false"}</Row>
        <Row label="VoiceRecorder ">{isVRPluginAvailable ? "true" : "false"}</Row>
        <Row label="Web Speech ">{isWebSpeech ? "true" : "false"}</Row>
        <Row label="Web MediaRecorder ">{isWebMediaSupported ? "true" : "false"}</Row>
      </Box>

      <Box title=" (TTS)">
        <div style={{ display: "flex", gap: 8, flexWrap: "wrap" }}>
          <button onClick={testNativeTTS}> TTS</button>
          <button onClick={testWebTTS}> Web TTS</button>
        </div>
        <div style={{ marginTop: 6 }}>
           TTS {ttsOk === null ? "()" : ttsOk ? "" : ""}
        </div>
        <div>
          Web TTS {webTtsOk === null ? "()" : webTtsOk ? "" : ""}
        </div>
        <div style={{ color: "#666", marginTop: 6 }}>
          / Google  ja-JP / zh-TW 
        </div>
      </Box>

      <Box title="">
        <div style={{ display: "flex", gap: 8, flexWrap: "wrap" }}>
          <button onClick={checkVRPermission}></button>
          <button onClick={requestVRPermission}></button>
          <button onClick={testNativeRecord}></button>
          <button onClick={testWebRecord}> Web </button>
        </div>
        <div style={{ marginTop: 6 }}>
          {vrPerm === null ? "()" : vrPerm ? "" : ""}
        </div>
        <div>{vrOk === null ? "()" : vrOk ? "" : `${vrErr ? ` - ${vrErr}` : ""}`}</div>
        <div>Web {webRecOk === null ? "()" : webRecOk ? "" : `${webRecErr ? ` - ${webRecErr}` : ""}`}</div>
        {recUrl && (
          <div style={{ marginTop: 8 }}>
            <audio ref={audioRef} src={recUrl} controls />
          </div>
        )}
      </Box>

      <Box title="">
        <pre style={{ whiteSpace: "pre-wrap" }}>{logs.join("\n")}</pre>
      </Box>
    </div>
  );
}
